enum DefaultStatus {
    Deleted = -1,
    Draft = 1,
    Published = 2,
    Secret = 3
}
const getStatusText = (status: DefaultStatus) => {
    switch (status) {
        case DefaultStatus.Deleted:
            return "已删除";
        case DefaultStatus.Draft:
            return "草稿";
        case DefaultStatus.Published:
            return "已发布";
        case DefaultStatus.Secret:
            return "私密";
        default:
            return "未知";
    }
}

export {DefaultStatus,getStatusText};