import util from "../../../../common/util";

interface IGlobal {
    showLoading: (text?: string) => void;
    hideLoading: () => void;
    getEditInfo: (key?: string) => {
        isEdit: boolean,
        id: string,
    };
    getFromDataObj: (selector: string) => any;

    alert: (text: string, obj?: any) => void;

    confirm: (message: string, onYes: any, onNo?: any) => void;

    alertError: (error: Array<any>) => void;

    layerDataMap: Array<any>;
    openDialog: (title: string, url: string, afterOpen?: any, onOk?: any, onCancel?: any) => void;

    closeDialog: (dialogResult: string, data?: any) => void;
}

const getLayer = () => {
    let layer = (<any>window).layer;
    if (layer) {
        return layer;
    }
    layer = (<any>parent).layer;
    return layer;
}

const Global: IGlobal = {
    showLoading: (text = null) => {
        var top = window.scrollY + "px";
        var left = window.scrollX + "px";

        $("html").addClass("disableScroll");

        if (text) {
            $(".loading .content>span").html(text);
        }

        $(".loading").css({
            "display": "flex",
            "top": top,
            "bottom": "-" + top,
            "left": left,
            "right": "-" + left
        });
    },

    hideLoading: () => {
        $(".loading").css("display", "none");
        $("html").removeClass("disableScroll");

    },

    getEditInfo: (key: string = "id") => {
        let id = util.pickUpUrlParams().get(key);
        let result: any = {
        }
        if (id) {
            result.isEdit = true;
            result.id = id;
        } else {
            result.isEdit = false;
            result.id = "";
        }
        return result;
    },
    getFromDataObj: (selector: any) => {
        let formData = $(selector).serialize();
        let formDataArr = formData.split("&");
        let forDataObj: any = {}
        formDataArr.forEach((item: any) => {
            let kv = item.split("=");
            forDataObj[kv[0]] = decodeURIComponent(kv[1]);
        });
        return forDataObj;
    },

    alert: (text, obj = null) => {
        let defaultObj = {
            time: 2000,
            title: "提示"
        }

        if (obj) {
            if (typeof obj === "function") {
                //callback
                getLayer().alert(text, defaultObj, obj);
            } else {
                $.extend(obj, defaultObj);
                getLayer().alert(text, obj);
            }
        } else {
            getLayer().alert(text, defaultObj);
        }
    },

    confirm: (message: string, onYes: any, onNo = null) => {
        getLayer().confirm(message,
            { "title": "提示" },
            (index: number) => {
                onYes();
                getLayer().close(index);
            }, (index: number) => {
                if (onNo) {
                    onNo();
                }
                getLayer().close(index);
            });
    },

    alertError: (error: Array<any>) => {
        var msg = error.map((item: any) => {
            return item.Message;
        }).join("<br/>>");
        Global.alert(msg,
            {
                title: "错误",
                icon: 2
            });
    },

    layerDataMap: [],
    openDialog: (title, url, afterOpen, onOk, onCancel) => {
        const layer = getLayer();
        let layerDataMap = Global.layerDataMap;
        const index = layer.open({
            type: 2,
            title: title ? title : false,
            closeBtn: 1,
            fixed: true,
            shade: 0.6,
            area: ['80%', '80%'],
            
            anim: 2,
            content: [url],
            success: (layero, dialogIndex) => {
                layerDataMap[dialogIndex] = {
                    success: onOk,
                    cancel: onCancel
                };
            },
            cancel: (index, layero) => {
                if (typeof onCancel === 'function') {
                    onCancel();
                }
            }    
        });
        if (typeof onOk === 'function') {
            afterOpen(index);
        }
    },
    closeDialog: (dialogResult, data) => {
        const parentLayer = (<any>parent).layer;
        var dialogIndex = parentLayer.getFrameIndex(window.name);
        parentLayer.close(dialogIndex);
        const parentGlobal = Global;
        var dialogConfig = parentGlobal.layerDataMap[dialogIndex];
        if (!dialogConfig) return;
        if (dialogResult === 'ok') {
            typeof dialogConfig.success == 'function' && dialogConfig.success(data);
        } else {
            typeof dialogConfig.cancel == 'function' && dialogConfig.cancel();
        }
        delete parentGlobal.layerDataMap[dialogIndex];
    },
}

export default Global;