import DataTableZ from "../components/dataTable";
import "../../../../common/extensions/global";
import Global from "../components/Global";
import { DefaultStatus, getStatusText } from "../common/DefaultStatus";

import "../../scss/pages/trivial-manage.scss";

(() => {

    let tableApi = "/api/Trivial/Table";
    let softDelApi = "/api/Trivial/Delete";
    let changeStatus = "/api/Trivial/ChangeStatus";
    let editView = "/BackCms/Trivial/Edit";

    let dt = new DataTableZ();

    const eventManager: any = (action: string, idArr: Array<string | number> = null) => {
        if(!idArr){
            idArr = dt.selectIdArr;
        }
        switch (action.toLocaleLowerCase()) {
            case "del":
                Global.showLoading();
                let result = DataTableZ.defautlDelete
                    (dt, softDelApi,
                        idArr,
                        (msg: any) => {
                            dt.reload();
                            Global.hideLoading();
                            dt.reload();
                        },
                        (jqXHR: any, textStatus: string, errorThrown: string) => {
                            console.log(jqXHR, textStatus, errorThrown);
                            alert("网络错误");
                            Global.hideLoading();
                        });
                if (!result) {
                    Global.hideLoading();
                }
                break;

            case "draft":
                $.post(changeStatus, {
                    idArr:idArr,
                    status: DefaultStatus.Draft
                }, (msg: any) => {
                    dt.reload();
                })
                break;
            case "published":
                $.post(changeStatus, {
                    idArr:idArr,
                    status: DefaultStatus.Published
                }, (msg: any) => {
                    dt.reload();
                })
                break;
            case "secret":
                $.post(changeStatus, {
                    idArr:idArr,
                    status: DefaultStatus.Secret
                }, (msg: any) => {
                    dt.reload();
                })
                break;

            default:
                break;
        }
    }

    const bindEvent = () => {
        $(".table-tool .filter").click((e: any) => {
            DataTableZ.defaultFilterEvent(dt);
        });

        $(".table-tool .reset").click((e: any) => {
            dt.resetFilter();
        });

        $(".del").click(() => {
            eventManager("del");
        });
    }

    /**每次重绘后为状态按钮添加事件 */
    const bindDrawCallBackEvent = () => {
        $("tbody .status").on("click", (e: any) => {
            let $this = $(e.target);
            let curStatus = parseInt($this.data("status"));
            let id = $this.data("id");
            switch (curStatus) {
                case DefaultStatus.Deleted:
                    eventManager('Draft', [id]);
                    break;
                case DefaultStatus.Draft:
                    eventManager('Published', [id]);
                    break;
                case DefaultStatus.Published:
                    eventManager('Secret', [id]);
                    break;
                case DefaultStatus.Secret:
                    eventManager('Draft', [id]);
                    return;
            }
        });

        

        $("tbody .row-del").on("click", (e: any) => {
            e.preventDefault();
            let id = $(e.target).data("id");
            eventManager("del", [id]);
        })        
    }


    let dtZConfig = {
        selector: "#dataTable",
        columnDefs: [
            {
                targets: 2,
                visible: false
            }
        ],
        "order": [[3, "desc"]],
        columns: [
            DataTableZ.checkBoxCol(),//0
            {
                "data": "Status", "title": "状态",
                render: (data: any, type: any, row: any, meta: any) => {
                    let color = "badge-info";
                    let status = data;

                    if (status === DefaultStatus.Published) {
                        color = "badge-success";
                    } else if (status === DefaultStatus.Secret) {
                        color = "badge-warning";
                    } else if (status === DefaultStatus.Deleted) {
                        color = "badge-danger";
                    }

                    let html = `<span class="badge ${color} status" data-status="${status}" data-id="${row["Id"]}">${getStatusText(status)}</span>`
                    return html;
                }
            },
            {
                "data": "Create", "title": "创建时间",
                render: (data: any, type: any, row: any, meta: any) => {
                    return data.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss");
                },
            },
            {
                "data": "Modify", "title": "修改时间",
                render: (data: any, type: any, row: any, meta: any) => {
                    return data.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss");
                }
            },
            {
                "data": "Content", "title": "内容",
                render: (data: any, type: any, row: any, meta: any) => {
                    return `<div class="row-content">${data}</div>`;
                },
            },
            {
                "data": "Id", "title": "操作",
                render: (data: any, type: any, row: any, meta: any) => {
                    return `<div class="btn-group ">
                    <a class="btn btn-secondary btn-sm" href="${editView}?Id=${data}">修改</a>
                    <button class="btn btn-danger btn-sm row-del" data-id="${data}">删除</button>                    
                </div>`;
                }
            }
        ],
        ajaxUrl: tableApi,
        initComplete: () => {
            bindEvent();
        },
        afterDraw: (setting: any) => {
            bindDrawCallBackEvent();
        }
    };
  
    dt.render(dtZConfig);
})();