///<reference path="../../../../node_modules/@types/datatables.net-select/index.d.ts"/>
///<reference path="../../../../node_modules/@types/datatables.net-scroller/index.d.ts"/>
///<reference path="../../../../node_modules/@types/datatables.net-buttons/index.d.ts"/>

import "../../scss/components/_dataTable.scss";

const language: DataTables.LanguageSettings = {
    "decimal": "",
    "emptyTable": "数据不存在",
    "info": "当前数据为 _START_ 到 _END_，共计 _TOTAL_ 条数据",
    "infoEmpty": "无数据",
    "infoFiltered": "(筛选自 _MAX_ 条数据)",
    "infoPostFix": "",
    "thousands": ",",
    "lengthMenu": "每页 _MENU_ 行",
    "loadingRecords": "加载中...",
    "processing": "处理中...",
    "search": "搜索:",
    "zeroRecords": "没有找到任何匹配的结果",
    "paginate": {
        "first": "第一页",
        "last": "最后一页",
        "next": "下一页",
        "previous": "前一页"
    },
    "aria": {
        "sortAscending": ": activate to sort column ascending",
        "sortDescending": ": activate to sort column descending"
    }
}

interface IDataTableZ {
    selector: string,
    columns: Array<DataTables.ColumnSettings>,
    ajaxUrl: string,
    columnDefs?:Array<DataTables.ColumnDefsSettings>,
    initComplete?: any,
    key?: string | number,
    moreDataFields?: Array<string>,
    afterDraw?: any,
    order?:any,
}

class DataTableZ {
    constructor() {
        this.selector = null;
        this.dataTable = null;
        this.where = {};
    }
    selector: any;
    dataTable: any;
    config: IDataTableZ;
    where: any;

    public get selectIdArr() {
        return this.getSelectIdArray();
    }

    render(config: IDataTableZ) {
        if (!config.key) {
            config.key = "Id";
        }
        this.config = config;
        let that = this;
        let order = config.order;
        if (!order) {
            order = [[3, 'desc']];
        }
        let dtSetting: DataTables.Settings | any= {
            "processing": true,
            "serverSide": true,
            ajax: (data: any, callback: any, settings: any) => {
                // console.log("ajax")
                if (config.moreDataFields && config.moreDataFields instanceof Array) {
                    for (let index = 0; index < config.moreDataFields.length; index++) {
                        const field = config.moreDataFields[index];
                        data.columns.push({
                            data: field,
                            name: "",
                            searchable: true,
                            orderable: true,
                        });
                    }
                }
                let whereArr = [];
                if (that.where) {
                    for (const key in that.where) {
                        if (that.where.hasOwnProperty(key)) {
                            const whereValue = that.where[key];
                            whereArr.push(`${key}=${whereValue}`);
                        }
                    }
                }
                data.where = whereArr;
                $.ajax({
                    url: config.ajaxUrl,
                    type: "Post",
                    data: data,
                    success: (msg: any) => {
                        if (msg.error) {

                        } else {
                            callback(
                                msg
                            );
                        }
                    }
                });
            },
            responsive: false,
            details: false,
            order: order,
            lengthMenu: [[10, 20, 50, 100, -1], [10, 20, 50, 100, "所有"]],
            pageLength: 20,
            "pagingType": "full_numbers",
            language: language,
            
            select: "multi",
            scrollY: "400",
            "scrollX": true,
            columnDefs:config.columnDefs,
            columns: config.columns,
            buttons: [{
                extend:'colvis',
                text:"列"
            }],
            // 'dom': 'Rlfrtip',
            dom: "R<'row'<'col-sm-12 col-md-6'Bl><'col-sm-12 col-md-6'f>>" +
                // "<'row'<'col-sm-12 col-md-12'B>>" +
                "<'row'<'col-sm-12'tr>>" +
                "<'row'<'col-sm-12 col-md-5'i><'col-sm-12 col-md-7'p>>",
            "initComplete": (settings: any, json: any) => {
                that.bingSearchEvent();
               
                $("#selectAll").click(() => {
                    if(!$("#selectAll").is(":checked")){
                        $("tbody tr.selected .row-selector input").click();
                    }else{
                        $("tbody tr:not(.selected) .row-selector input").click();
                    }                    
                });
                typeof config.initComplete === 'function'
                    && config.initComplete();
            }
        };

        this.dataTable = $(config.selector).DataTable(dtSetting);
        this.dataTable.buttons().container()
            .appendTo($('.col-sm-6:eq(0)', this.dataTable.table().container()));

        let lastSelectedLength = 0;
        this.dataTable.on('draw', function (e: any) { 
            typeof config.afterDraw === "function" && config.afterDraw(e,that.dataTable.rows().data());
            $(config.selector + ' tbody .row-selector>input').on('click', function () {
                $(this).parents('tr').toggleClass('selected');
                //console.log($(this).parents('tr'));
                // $(this).toggleClass('selected');
                that.getSelectIdArray();
            });

            $(".row-selector input").change(() => {
                let curLength = $("tbody tr:not(.selected)").length;
                if (curLength === 0) {
                    $("#selectAll").prop("checked", "checked");
                } else {
                    $("#selectAll").prop("checked", null);
                }
            });
        });
    }


    filter(where: any = null) {
        this.where = {};
        if (where) {
            for (const key in where) {
                if (where.hasOwnProperty(key)) {
                    const whereValue = where[key];
                    this.where[key] = whereValue;
                }
            }
        }
        this.search();
    }

    search() {
        let searchVal: string = <string>$('#dataTable_filter input').val();
        if (searchVal === "" || searchVal) {
            this.dataTable.search(searchVal).draw();
        } else {
            this.dataTable.ajax.reload();
        }
    }

    private innerSearch(searchVal: string = "") {
        this.dataTable.search(searchVal).draw();
    }

    reset() {
        this.where = {};
        this.dataTable.search("").draw();
    }

    resetSearch() {
        this.innerSearch("");
    }

    resetFilter() {
        $(".filter-container>select>option:first-child").prop("selected", true);
        this.where = {};
        this.search();
    }

    reload() {
        this.dataTable.ajax.reload();
    }

    /**
     * 解决搜索框事件
     */
    bingSearchEvent() {
        $('#dataTable_filter input').unbind();
        let that = this;
        $('#dataTable_filter input').on('keyup', function (e: any) {
            if (e.keyCode == 13) {
                that.search();
            }
        });
        $('#dataTable_filter input').on("input", (e: any) => {
            if (!$(e.target).val()) {
                that.search();
            }
        })
    }

    /**
     * 获取所有选择的行的id
     */
    getSelectIdArray() {
        let selectDataArr = this.dataTable.rows('.selected').data();
        let key = this.config.key;
        let idArr = selectDataArr.map((item: any) => {
            return item[key];
        });
        if (idArr.length > 0) {
            return idArr.splice(0, idArr.length);
        } else {
            return [];
        }
    }

    static checkBoxCol(key: any = "Id") {
        return {
            data: `${key}`,
            width: "40px",
            orderable: false,
            searchable: false,
            title:`<div class="custom-control custom-checkbox"><input type="checkbox" class="custom-control-input" id="selectAll"><label class="custom-control-label" for="selectAll"></label></div>`,
            render: (data: any, type: any, row: any, meta: any) => {
            //     let html = `<label class="row-selector">
            //         <input type="checkbox" value="${data}"/>
            //         <span></span>
            //   </label>`;
              let html = 
                `<div class="custom-control custom-checkbox row-selector">
                    <input type="checkbox" class="custom-control-input" id="row_${data}" value="${data}">
                    <label class="custom-control-label" for="row_${data}"></label>
                 </div>`;
                return html;
            }
        };
    }

    static defaultFilterEvent(dt: DataTableZ) {
        let where: any = {};
        $(".filter-container>select").each((index: number, ele: HTMLElement) => {
            let $select = $(ele);
            let val: string = <string>$select.val();
            let defaultVal: string = <string>$select.find("option").eq(0).val();
            if (val !== defaultVal) {
                where[$select.attr("name")] = val
            }
        })
        $(".filter-container>.bootstrap-select>select").each((index: number, ele: HTMLElement) => {
            let $select = $(ele);
            let val: string = <string>$select.val();
            let defaultVal: string = <string>$select.find("option").eq(0).val();
            if (val !== defaultVal) {
                where[$select.attr("name")] = val
            }
        })
        //console.log(where);
        dt.filter(where)
    }

    static defautlDelete(dt: DataTableZ, url: string, idArr:any = null,
        success: (msg: any) => void = null,
        error: (jqXHR: any, textStatus: string, errorThrown: string) => void = null) {

        let selectIdArr: any;
        if (idArr) {
            selectIdArr = idArr;
        } else {
            selectIdArr = dt.selectIdArr;
        }
        if (selectIdArr.length === 0) {
            alert("未选择项目");
            return false;
        }
        $.ajax({
            url: url,
            data:{
                idArr:selectIdArr
            },
            type: "post",
            success: (msg: any) => {
                typeof success === "function" && success(msg);
            },
            error: (jqXHR: any, textStatus: string, errorThrown: string) => {
                typeof error === "function" && 
                    error(jqXHR, textStatus, errorThrown);
            }
        });
        return true;
    }
}
export default DataTableZ;